.card {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: $card_size;
  padding: 32px 16px 16px;
  border: 1px solid $grey_color;
  border-radius: 5px;
  transition: all $transition_time;

  &__image {
    display: flex;
    width: 160px;
    height: 134px;
    padding-inline: 3px;
    margin-bottom: 40px;
  }

  &__info {
    display: flex;
    flex-direction: column;

    &--title {
      color: $dark_blue_color;
      font-size: 12px;
      font-weight: 500;
      line-height: 18px;
      justify-content: center;
      margin-top: 0;
      margin-bottom: 4px;
    }

    &--code {
      color: $secondary_color;
      font-size: 10px;
      line-height: 14px;
      font-weight: 400;
      margin-bottom: 16px;
      margin-top: 0;
    }

    &--rating {
      box-sizing: border-box;
      display: flex;
      justify-content: space-between;
      flex-direction: row;
      align-items: center;
      margin-bottom: 24px;

      &--reviews {
        text-align: right;
        font-weight: 400;
        font-size: 10px;
        line-height: 14px;
        color: $dark_blue_color;
        height: 16px;
      }
    }

    &--price {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-bottom: 16px;

      &--title {
        font-weight: 400;
        font-size: 12px;
        line-height: 18px;
        color: $secondary_color;
        margin: 0;
      }

      &--cost {
        font-size: 16px;
        font-weight: 700;
        line-height: 18px;
        color: $dark_blue_color;
        margin: 0;
      }
    }
  }

  &__button {
    box-sizing: border-box;
    background-color: $blue_color;
    height: 40px;
    width: 100%;
    text-decoration: none;
    text-transform: uppercase;
    text-align: center;
    line-height: 40px;
    color: $white_color;
    justify-content: center;
    border-radius: 5px;
    font-size: 14px;
    font-weight: 700;

    &:hover {
      background-color: $white_color;
      color: $blue_color;
      border: 1px solid $blue_color;
      transition: $transition_time;
    }
  }

  &:hover {
    transform: scale(1.2);
    transition: $transition_time;

    .card__info--title {
      color: $title_color_hover;
      transition: $transition_time;
    }
  }
}
