* {
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 15px;
}

.header {
  box-sizing: border-box;
  padding-top: 0 50px;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo-image {
  justify-content: center;
  height: 40px;
  margin-left: 50px;
  display: flex;
}

.nav {
  align-content: space-between;
  margin-right: 50px;
  display: flex;
}

.nav__list {
  text-align: center;
  text-transform: uppercase;
  align-items: center;
  list-style: none;
  display: flex;
}

.nav__item {
  text-align: center;
  justify-content: center;
  margin-left: 20px;
  display: flex;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  text-align: center;
  color: #060b35;
  height: 60px;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
}

.nav__link:hover {
  color: #00acdc;
  transition: all .3s;
}

.is-active {
  color: #00acdc;
  display: block;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 4px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  max-width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
  padding-inline: 3px;
  display: flex;
}

.card__info {
  flex-direction: column;
  display: flex;
}

.card__info--title {
  color: #060b35;
  justify-content: center;
  margin-top: 0;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__info--code {
  color: #616070;
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__info--rating {
  box-sizing: border-box;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__info--rating--reviews {
  text-align: right;
  color: #060b35;
  height: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__info--price {
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__info--price--title {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__info--price--cost {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  transition: all .3s;
}

.card:hover {
  transition: all .3s;
  transform: scale(1.2);
}

.card:hover .card__info--title {
  color: #34568b;
  transition: all .3s;
}

.stars {
  height: 16px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
